.main-video {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
}

.main-container {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: cover;
}

.main-container>h1 {
    color: rgb(51, 63, 95);;
    text-shadow: 3px 2px 2px rgb(0, 0, 0);
    font-family: 'Stalinist One', cursive;
    font-size: 65px;
    letter-spacing: 3px;
    font-weight: lighter;
    margin-top: -100px;
}

.main-container>p {
    margin-top: 8px;
    color: rgb(136, 16, 16);;
    text-shadow: 0px 2px 2px rgb(0, 0, 0);
    font-size: 32px;
}

.main-btns {
    margin-top: 32px;
}

.main-btns .btn {
    margin: 6px;
}

.fa-play-circle {
    margin-left: 4px;
}

@media screen and (max-width: 1024px) {
    .main-container>h1 {
        font-size: 65px;
        margin-top: -150px;
    }
    .main-container>p {
        font-size: 30px;
        text-align: center;
    }
}


@media screen and (max-width: 960px) {
    .main-container>h1 {
        font-size: 58px;
        margin-top: -150px;
    }
    .main-container>p {
        font-size: 27px;
        text-align: center;
    }
}

@media screen and (max-width: 768px) {
    .main-container {
        padding: 2rem;
    }
    .main-container>h1 {
        font-size: 40px;
        margin-top: -100px;
    }
    .main-container>p {
        font-size: 25px;
        text-align: center;
    }
    .btn-mobile {
        display: block;
        text-decoration: none;
    }
    .btn {
        width: 80%;
    }
}

@media screen and (max-width: 615px) {
    .main-container {
        padding: 6rem;
    }
    .main-container>h1 {
        font-size: 35px;
        margin-top: -100px;
    }
    .main-container>p {
        font-size: 25px;
        text-align: center;
    }
    .btn-mobile {
        display: block;
        text-decoration: none;
    }
    .btn {
        width: 70%;
    }
    
}